
import React, { PureComponent, Suspense } from "react";
import TopCard from "../layout/common/topCard"; 
import Tabheader from "../layout/common/tabheader";
import { Card, CardBody, CardHeader } from "react-bootstrap";
import { connect } from "react-redux";
import { reArrangeSeries } from "../../../../utils";
import ImageSkeleton from "../layout/skeleton/imageSkeleton";

const PostOverview = React.lazy(() =>
  import("../components/activityHub/postOverview")
);

const PerfomingPost = React.lazy(() =>
  import("../components/activityHub/perfomingPost")
);

const AssetsTypeLineChart = React.lazy(() =>
  import("../layout/common/assetsTypeLineChart")
);

class Widget extends PureComponent {

  render() {
    const { get_top_reacted_posts, get_post_overview_loader, get_post_overview_data, get_performing_post_data, get_reacted_posts_loader, get_performing_post_loader, get_web_analytic_data, get_web_analytic_loader } = this.props.analyticsRecords;
    const contentAnalytics = get_web_analytic_data && Object.keys(get_web_analytic_data).length && reArrangeSeries(get_web_analytic_data,3);

    return (
      <>
            <Tabheader />
            <div className="row">
              <TopCard loader={get_web_analytic_loader} data={get_web_analytic_data} col={4} res={3}  />
            </div>  

             <div className="row">
                <div className="col-lg-8 col-12 mb-2">
                  <Card className="sw-card-custom gutter-b p-5 card-shadow">
                    <CardHeader className="p-0 pb-3">
                      <h6 class="card-label font-weight-bolder">
                        Content Analytis
                      </h6>
                    </CardHeader>
                    <CardBody className="p-0">
                       {  get_web_analytic_loader ? <ImageSkeleton img={2} />
                            :
                         <Suspense fallback={<></>}>
                           <AssetsTypeLineChart get_web_analytic_loader={get_web_analytic_loader}  contentAnalyticsData={contentAnalytics} type={1}/> 
                         </Suspense>
                         }
                    </CardBody>
                  </Card>
                </div>
                <Suspense fallback={<></>}>
                  <PostOverview get_post_overview_loader={get_post_overview_loader} get_post_overview_data={get_post_overview_data} />
                </Suspense>
              </div>
             
              <Suspense fallback={<></>}>
                <PerfomingPost fetchApi={'getPrfomingPostMore'} type={1} title={'Top Perfoming Posts'} loader={get_performing_post_loader} {...get_performing_post_data} />
              </Suspense>  

               <Suspense fallback={<></>}>
                <PerfomingPost fetchApi={'getreactedpostsMore'} type={2} title={'Top Reacted Posts'} loader={get_reacted_posts_loader} {...get_top_reacted_posts} />
               </Suspense>  
      </>
    );
  }
}

const displayStateprops = (state) => { 
  return {
    analyticsRecords: state.analyticsRecords,
  };
};

export default connect(displayStateprops, '')(Widget);
